import React, { useState } from "react";
import { useEffect } from "react";
import { Fade } from "react-awesome-reveal";
import { Link } from "react-router-dom";
import { AboutUs } from "../../static/staticURLS";

import Clientele from "../common/Clientele";

function About() {
  let section2Video = AboutUs?.section2Video;
  let displayTab = AboutUs?.Team;

  const [isReadMore, setIsReadMore] = useState(true);
  const [readMoreId, setReadMoreId] = useState([]);
  const [readMorePara, setReadMorePara] = useState(false);

  const toggleReadMorePara = () => {
    setReadMorePara(!readMorePara);
  };

  const toggleReadMore = (id) => {
    setIsReadMore(!isReadMore);
    let tempReadMore = structuredClone(readMoreId);
    if (readMoreId?.length === 0 || !readMoreId) {
      tempReadMore.push(id);
      setReadMoreId(tempReadMore);
    } else {
      tempReadMore.map((addedId) => {
        if (addedId == id) {
          tempReadMore = tempReadMore?.filter((item) => item != id);
        } else {
          tempReadMore?.push(id);
        }
      });
    }
    setReadMoreId(tempReadMore);
  };


  return (
    <>
      <main>
        {/* Top Section */}
        <div
          className="relative pt-16 pb-32 flex content-center items-center justify-center"
          style={{
            minHeight: "100vh",
          }}
        >
          <div
            className="absolute top-0 w-full h-full bg-center bg-cover header_bg_opacity"
            style={{
              backgroundImage: `url(${AboutUs?.AboutUsMainBackground})`,
            }}
          >
            <span
              id="blackOverlay"
              className="w-full h-full absolute opacity-70 bg-black"
            ></span>
          </div>
          <div className="container relative mx-auto">
            <div className="items-center flex flex-wrap">
              <div className="w-full mt-24 ml-auto mr-auto text-center">
                <div className="headerText">
                  <h1 className="text-white font-semibold text-5xl pb-6">
                    Your story starts with us
                  </h1>
                  <p className="mt-4 text-center text-xl text-gray-300">
                    We are a team of commercial photographers and filmmakers
                    based out of Thane, Mumbai. &nbsp;
                    {!readMorePara ? (
                      <>
                        {AboutUs?.AboutUsPara.slice(0, 206)}
                        <span
                          onClick={toggleReadMorePara}
                          className="readMoreLink italic"
                        >
                          &nbsp;...Read more
                        </span>
                      </>
                    ) : (
                      <>
                        {AboutUs?.AboutUsPara}
                        <span
                          onClick={toggleReadMorePara}
                          className="readMoreLink italic"
                        >
                          &nbsp;...Read less
                        </span>
                      </>
                    )}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Midddle Section with insta video embedd */}
        {/* <section className="pb-20 bg-gray-100 -mt-32">
          <div className="container mx-auto px-4">
            <Fade direction="up">
              <div className="flex flex-wrap items-center mt-32">
                <div className="w-full md:w-5/12 px-4 mr-auto ml-auto">
                  <h3 className="text-3xl mb-2 font-semibold leading-normal">
                    Working with us is a pleasure
                  </h3>
                  <p className="text-lg font-light leading-relaxed mt-4 mb-4 text-gray-700">
                    {AboutUs?.AboutUsPara}
                  </p>
                  <Link to="/Contact" className="font-bold text-gray-800 mt-8">
                    Get In Touch!
                  </Link>
                </div>

                <div className="w-full md:w-4/12 px-4 mr-auto ml-auto mt-6">
                  <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 shadow-lg rounded-lg bg-pink-600">
                    <video className="aboutUsVideoSection2" controls>
                      <source src={section2Video} type="video/mp4"></source>
                    </video>
                    <blockquote className="relative p-8 mb-4">
                      <svg
                        preserveAspectRatio="none"
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 583 95"
                        className="absolute left-0 w-full block"
                        style={{
                          height: "95px",
                          top: "-94px",
                        }}
                      >
                        <polygon
                          points="-30,95 583,95 583,65"
                          className="text-pink-600 fill-current"
                        ></polygon>
                      </svg>
                      <h4 className="text-xl font-bold text-white">
                        Top Notch Services
                      </h4>
                      <p className="text-md font-light mt-2 text-white">
                        That frame of mind that you need to make fine pictures
                        of a very wonderful subject; you cannot do it by not
                        being lost yourself.
                        <span className="italics">~Dorothea Lange</span>
                      </p>
                    </blockquote>
                  </div>
                </div>
              </div>
            </Fade>
          </div>
        </section> */}

        <section className="relative py-20">
          {/* <div
            className="bottom-auto top-0 left-0 right-0 w-full absolute pointer-events-none overflow-hidden -mt-20"
            style={{ height: "80px" }}
          >
            <svg
              className="absolute bottom-0 overflow-hidden"
              xmlns="http://www.w3.org/2000/svg"
              preserveAspectRatio="none"
              version="1.1"
              viewBox="0 0 2560 100"
              x="0"
              y="0"
            >
              <polygon
                className="text-white fill-current"
                points="2560 0 2560 100 0 100"
              ></polygon>
            </svg>
          </div> */}
          {/* Team mates */}
          <div className="container mx-auto px-4">
            {displayTab?.map((tab) => {
              return (
                <>
                  <div className="hero min-h-screen/3 mt-12 pb-12" key={tab.id}>
                    <Fade direction={`${tab.id % 2 != 0 ? "right" : "left"}`}>
                      <div
                        className={`hero-content flex-col ${
                          tab.id % 2 != 0
                            ? "lg:flex-row-reverse"
                            : "lg:flex-row"
                        }`}
                      >
                        <img
                          src={tab?.displayPhoto}
                          className="md:max-w-xl rounded-lg shadow-2xl"
                        />
                        <div
                          className={`p-5 ${
                            tab.id % 2 != 0 ? "text-right" : "text-left"
                          } `}
                        >
                          <h1 className="font-primary text-3xl lg:text-3xl xl:text-4xl">
                            {tab?.name}
                          </h1>
                          {/* <hr className="lineHighlight"></hr> */}
                          <p className="py-6 font-secondary  md:text-xs lg:text-lg italic tracking-wide">
                            {!readMoreId?.includes(tab?.id) ? (
                              tab?.aboutMe.slice(0, 206) + "..."
                            ) : (
                              <>
                                {tab?.aboutMe}
                                <div className="grid grid-cols-4 gap-4 list-disc list-inside text-gray-500 dark:text-gray-400">
                                  {tab?.pointer?.map((points, index) => {
                                    return (
                                      <>
                                        <li key={index}>{points}</li>
                                      </>
                                    );
                                  })}
                                </div>
                                {tab?.aboutMeContinue}
                              </>
                            )}
                          </p>
                          <button
                            className="btn bg_slider rounded-none pr-6 pl-6"
                            onClick={() => toggleReadMore(tab?.id)}
                          >
                            {!readMoreId?.includes(tab?.id)
                              ? "Show more"
                              : " Show less"}
                          </button>
                        </div>
                      </div>
                    </Fade>
                  </div>
                </>
              );
            })}
          </div>

          {/* Clientele */}
          <Clientele />
        </section>
      </main>
    </>
  );
}

export default About;
