import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import { Events, Interiors, Weddings } from "../../static/staticURLS";
import { Fade } from "react-awesome-reveal";
import { removeSpaceTitleCase } from "../../hooks/removeSpaceTitleCase";

function InteriorEventsLowerSection() {
  let location = useLocation();
  let pathName = location?.pathname?.slice(1);
  const [displayTab, setDisplayTab] = useState([]);
  useEffect(() => {
    if (pathName === "Events") {
      setDisplayTab(Events);
    } else if (pathName === "Interiors") {
      setDisplayTab(Interiors);
    } else if (pathName === "Weddings") {
      setDisplayTab(Weddings);
    }
  }, [location]);

  return (
    <>
      {/* Section - 1 */}
      {displayTab?.map((tab) => {
        return (
          <>
            <div className="hero min-h-screen/2  mt-12 pb-5">
              <Fade direction={`${tab.id % 2 != 0 ? "right" : "left"}`}>
                <div
                  className={`hero-content flex-col ${
                    tab.id % 2 != 0 ? "lg:flex-row-reverse" : "lg:flex-row"
                  }`}
                >
                  <img
                    src={tab?.cover}
                    className="md:max-w-xl rounded-lg shadow-2xl"
                  />
                  <div
                    className={`p-5 sm:text-center ${
                      tab.id % 2 != 0 ? "text-right" : "text-left"
                    } `}
                  >
                    <h1 className="font-primary text-3xl lg:text-3xl xl:text-4xl relative">
                      {tab?.heading}
                    </h1>
                    {/* <hr className="lineHighlight"></hr> */}
                    <p className="py-6 font-secondary  md:text-xs lg:text-lg italic tracking-wide">
                      {tab?.para}
                    </p>
                    <Link to={`${removeSpaceTitleCase(tab?.heading)}`}>
                      <button className="btn bg_slider rounded-none pr-6 pl-6">
                        Explore
                      </button>
                    </Link>
                  </div>
                </div>
              </Fade>
            </div>
          </>
        );
      })}
    </>
  );
}

export default InteriorEventsLowerSection;
