import React, { useEffect, useState } from "react";
import axios from "axios";
import { Fade } from "react-awesome-reveal";
import { AWS_Django_API_URL } from "../../static/staticURLS";
const Clientele = () => {
  const [error, setError] = useState(false);
  const [clientele, setClienetele] = useState([]);
  const [loading, setLoading] = useState(false);
  // Get Clientele List from the BE
  const getClientele = async () => {
    setLoading(true);
    let clienteleList = await axios.get(
      `/api/About/Clientele`
      , { crossdomain: true });
    if (clienteleList?.status === 200) {
      setClienetele(clienteleList?.data);
      setLoading(false);
    } else {
      setError(true);
      setLoading(false);
    }
  };
  useEffect(() => {
    getClientele();
    setLoading(false);
  }, []);
  return (
    <div className="mt-40">

        <div className="max-w-xl mb-10 md:mx-auto sm:text-center lg:max-w-2xl md:mb-12">
          <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold leading-none tracking-tight text-black sm:text-4xl md:mx-auto text-center">
            <span className="relative text-center">
              <svg
                viewBox="0 0 52 24"
                fill="currentColor"
                className="absolute top-0 left-0 z-0 hidden w-32 -mt-8 -ml-20 text-blue-gray-100 lg:w-32 lg:-ml-28 lg:-mt-10 sm:block"
              >
                <defs>
                  <pattern
                    id="903f4a9e-7ac3-441c-9613-04c15fcc0a14"
                    x="0"
                    y="0"
                    width=".135"
                    height=".30"
                  >
                    <circle cx="1" cy="1" r=".7" />
                  </pattern>
                </defs>
                <rect
                  fill="url(#903f4a9e-7ac3-441c-9613-04c15fcc0a14)"
                  width="52"
                  height="24"
                />
              </svg>
              <span className="relative">OUR </span>
            </span>{" "}
            CLIENTELE
          </h2>
          <p className="text-base text-gray-700 md:text-lg italic">
            We go extra mile to capture the special moments of your life.
          </p>
        </div>
        {loading ? (
          <div class="spinner"></div>
        ) : (
          <Fade direction="up">
          <div className="grid grid-cols-2 gap-5 row-gap-6 mb-10 sm:grid-cols-3 lg:grid-cols-6">
            {clientele?.map((clients, index) => {
              return (
                <>
                  <div className="text-center" key={index}>
                    <div className="flex items-center justify-center w-24 h-24 mx-auto mb-4 rounded-full bg-white sm:w-24 sm:h-24">
                      <img
                        src={clients?.Image}
                        alt={clients?.Name}
                        className="rounded-full"
                      />
                    </div>
                  </div>
                </>
              );
            })}
          </div>
      </Fade>
        )}
    </div>
  );
};

export default Clientele;
