// About Us Page Constants
export const AboutUs = {
  AboutUsPara:
    "Still Minute is an extension of the lives of Akash and Siddharth. In 2016, our shared passion for photography led us to embark on this journey of visual storytelling. We have constantly challenged ourselves and the commercial trends, to set us apart and create images that bring happiness, evoke memories, surprise you and also bring value to your brand. Our work has always been multi faceted, ranging from Weddings, Events, Interiors, Fine Art, Food to Product and Industrial photography. With our eye to perfection, advanced in-house lighting, photography and post production facilities, combined with an experienced team, we adapt to your requirement and provide a hassle free experience. Like every other photography team, we aspire to grow and excel at our craft but we go the extra mile, to build a relation and an extended family that stays forever. Welcome to our family!",
  AboutUsMainBackground: AboutUsMainBackground,
  section2Video: section2Video,
  Team: [
    {
      id: 1,
      name: "AKASH AJGAONKAR",
      aboutMe:
        "Before I dive into myself, let me confess that I havent plagiarised Farhans life from the movie 3 Idiots. And now you have a fair idea of what I am about to say. Having educated from a Science background, I took up Engineering and graduated from the College of Engineering Pune in the field of Instrumentation and Control. During the 4 years of college, I realised that my passion for creation would make me a good engineer, but a better photographer and so began my journey as Still Minute Photography with Siddharth. The love for colours, compositions and stories has always made a better part of my life and photography provided me the opportunity to express myself. My process of making a photograph transcends the mere need to capture the moment, to understand it, finding a meaning and creating a story. I believe that art is a universal language of creation and every bit of life provides inspiration to create. My history with engineering, the walks Ive had on the beach, the films Ive watched, the people in my life, the colours in my closet, the climate outside my window and the music Ive heard, every bit of it inspires my photography and Still Minute is my mode of communication.",
      displayPhoto: AkashPic,
    },
    {
      id: 2,
      name: "SIDDHARTH GUNDAWAR",
      aboutMe:
        "Siddharth Gundawar, a Bachelor of Fine Arts graduate from Symbiosis School of Photography was enamored by the art of Photography during one of his assignments.However, this love-story dates back to 2008. The seeds of affection were planted right at the moment when he got his hands on a digicam. The secluded green areas of Thane and the vast blue skies further fuelled his affection Limitations of digicam could not stop him from capturing nature with both its biotic and abiotic elements and taught him the fine skill of patience. Flirting with the vital intricacies of life in macro photography, he refined his observational skills. He had just started but his undying spirit to explore was overlooking every risk that came. To finally channelize the energies into the right direction, Symbiosis stepped in. Symbiosis taught him to see the light for the very first time.",
      pointer: [
        "Abstraction",
        "Composition",
        "Lines",
        "Shapes",
        "Colours",
        "Forms",
        "Symmetry",
        "Geometry",
        "Texture",
        "Pattern",
        "Minimalism",
        "Reflections",
        "Disorder",
        "Confusion",
        "Distortion",
        "Chaos",
      ],
      aboutMeContinue:
        "Had heard these words quite a lot of time but relating them to images was done by Siddharth during his undergraduate days. As a layman, clicking a photograph might seem simpler but come to think of it, it is like live editing, consciously or unconsciously choosing what you put in the frame. The real meaning of a photograph found out by this guy was very spiritual and had a broad perspective. The intricacy of the thought behind any photograph that is click is equivalent to the intricacy of the photograph and Siddharth covers all the aspects of it. To give you a clear picture, here is an insight into what is going on his mind: What makes a photograph? How do you just take a photograph? Luck? Concept? Meaning? Do all things need to have a meaning? Ready to capture that? What is beauty? Is it subjective? Who decides? Who is right? Who is wrong? But what is right or wrong? Authority? Rules? Laws? Humans? Brain? Thought? What is a reality? Can you stop time? Can you go into the past? Yes, just pull up a photograph. Can you see someones soul? Look closely. What do you see? Pixels too small to see or the things which you want to see? Another illusion? The thoughts are endless and so is his vision. After graduating and clicking a gazillion photographs later, Siddharth has found a way to appreciate the strangeness of this world through his camera. He is a strong believer that through the Power of Photography one can alter the reality and create an illusion. Hes too shy, gets awkward and uncomfortable if someone steps into his zone. Thus, to know him better, keep in mind that he also likes philosophy, astrophysics, exercising, meditation, cycling, trekking, music, graffiti, world cinema and most importantly, dogs. While youre reading this, He is out there somewhere thinking, shooting and exploring.",
      displayPhoto: SiddharthPic,
    },
  ],
};

