import React from "react";
import { NavLink, useLocation } from "react-router-dom";
// Static Files Import
import { webLogo } from "../../../static/staticURLS";

// Import Icons
import { AiFillFacebook, AiFillInstagram } from "react-icons/ai";
import { BsYoutube } from "react-icons/bs";

function Navbar() {
  const location = useLocation();
  return (
    <>
      <div className="navbar bg-base-100 sticky top-0 z-30">
        <div className="navbar-start hidden lg:flex">
          <ul className="menu menu-horizontal p-0">
            <li tabIndex={0}>
              <a>
                STILLS
                <svg
                  className="fill-current"
                  xmlns="http://www.w3.org/2000/svg"
                  width="20"
                  height="20"
                  viewBox="0 0 24 24"
                >
                  <path d="M7.41,8.58L12,13.17L16.59,8.58L18,10L12,16L6,10L7.41,8.58Z" />
                </svg>
              </a>
              <ul className="p-2" style={{ background: "white" }}>
                <li>
                  <NavLink
                    to="/Product"
                    className={
                      location.pathname === "/Product"
                        ? "active"
                        : "inactiveLink"
                    }
                  >
                    PRODUCT
                  </NavLink>
                </li>
                <li>
                  <NavLink
                    to="/Interiors"
                    className={
                      location.pathname === "/Interiors"
                        ? "active"
                        : "inactiveLink"
                    }
                  >
                    INTERIORS
                  </NavLink>
                </li>
                <li>
                  <NavLink
                    to="/Events"
                    className={
                      location.pathname === "/Events"
                        ? "active"
                        : "inactiveLink"
                    }
                  >
                    EVENTS
                  </NavLink>
                </li>
                <li>
                  <NavLink
                    to="/Weddings"
                    className={
                      location.pathname === "/Weddings"
                        ? "active"
                        : "inactiveLink"
                    }
                  >
                    WEDDINGS
                  </NavLink>
                </li>
                <li>
                  <NavLink
                    to="/Food"
                    className={
                      location.pathname === "/Food" ? "active" : "inactiveLink"
                    }
                  >
                    FOOD
                  </NavLink>
                </li>
                <li>
                  <NavLink
                    to="/FineArt"
                    className={
                      location.pathname === "/FineArt"
                        ? "active"
                        : "inactiveLink"
                    }
                  >
                    FINE ART
                  </NavLink>
                </li>
              </ul>
            </li>
            <li>
              <NavLink
                to="/Films"
                className={
                  location.pathname === "/Films" ? "active" : "inactiveLink"
                }
              >
                FILMS
              </NavLink>
            </li>
            <li>
              <a
                href="https://stillminute.blogspot.com/"
                target={"__blank"}
                className={
                  location.pathname === "/Blog" ? "active" : "inactiveLink"
                }
              >
                BLOG
              </a>
            </li>
            <li>
              <NavLink
                to="/About"
                className={
                  location.pathname === "/About" ? "active" : "inactiveLink"
                }
              >
                ABOUT
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/Contact"
                className={
                  location.pathname === "/Contact" ? "active" : "inactiveLink"
                }
              >
                CONTACT
              </NavLink>
            </li>
          </ul>
        </div>
        <div className="navbar-center">
          <div className="dropdown">
            <label tabIndex={0} className="btn btn-ghost lg:hidden">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M4 6h16M4 12h8m-8 6h16"
                />
              </svg>
            </label>
            <ul
              tabIndex={0}
              className="menu menu-compact dropdown-content mt-3 p-2 shadow bg-base-100 rounded-box w-52"
            >
              <li tabIndex={0}>
                <a className="justify-between">
                  STILLS
                  <svg
                    className="fill-current"
                    xmlns="http://www.w3.org/2000/svg"
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                  >
                    <path d="M8.59,16.58L13.17,12L8.59,7.41L10,6L16,12L10,18L8.59,16.58Z" />
                  </svg>
                </a>
                <ul className="p-2" style={{ background: "white" }}>
                  <li>
                    <NavLink
                      to="/Product"
                      className={
                        location.pathname === "/Product"
                          ? "active"
                          : "inactiveLink"
                      }
                    >
                      PRODUCT
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/Interiors"
                      className={
                        location.pathname === "/Interiors"
                          ? "active"
                          : "inactiveLink"
                      }
                    >
                      INTERIORS
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/Events"
                      className={
                        location.pathname === "/Events"
                          ? "active"
                          : "inactiveLink"
                      }
                    >
                      EVENTS
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/Weddings"
                      className={
                        location.pathname === "/Weddings"
                          ? "active"
                          : "inactiveLink"
                      }
                    >
                      WEDDINGS
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/Food"
                      className={
                        location.pathname === "/Food"
                          ? "active"
                          : "inactiveLink"
                      }
                    >
                      FOOD
                    </NavLink>
                  </li>
                  <li>
                    <NavLink
                      to="/FineArt"
                      className={
                        location.pathname === "/FineArt"
                          ? "active"
                          : "inactiveLink"
                      }
                    >
                      FINE ART
                    </NavLink>
                  </li>
                </ul>
              </li>
              <li>
                <NavLink
                  to="/Films"
                  className={
                    location.pathname === "/Films" ? "active" : "inactiveLink"
                  }
                >
                  FILMS
                </NavLink>
              </li>
              <li>
                <a
                  href="https://stillminute.blogspot.com/"
                  target={"__blank"}
                  className={
                    location.pathname === "/Blog" ? "active" : "inactiveLink"
                  }
                >
                  BLOG
                </a>
              </li>
              <li>
                <NavLink
                  to="/About"
                  className={
                    location.pathname === "/About" ? "active" : "inactiveLink"
                  }
                >
                  ABOUT
                </NavLink>
              </li>
              <li>
                <NavLink
                  to="/Contact"
                  className={
                    location.pathname === "/Contact" ? "active" : "inactiveLink"
                  }
                >
                  CONTACT
                </NavLink>
              </li>
            </ul>
          </div>
          <NavLink
            to="/"
            className="btn btn-ghost normal-case text-xl webLogo activeLogo"
          >
            <img src={webLogo} className="webLogo" />
          </NavLink>
        </div>
        <div className="navbar-end hidden lg:flex">
          <div>
            <ul className="home-about-social-links">
              <li className="social-icons">
                <a
                  href="https://www.youtube.com/channel/UCZISLslvzpPI9ebD7lMWNEQ?view_as=subscriber"
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <BsYoutube className="text-2xl" />
                </a>
              </li>
              <li className="social-icons">
                <a
                  href="https://www.facebook.com/sminutep"
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <AiFillFacebook className="text-2xl" />
                </a>
              </li>
              <li className="social-icons">
                <a
                  href="https://www.instagram.com/stillminute_/"
                  target="_blank"
                  rel="noreferrer"
                  className="icon-colour  home-social-icons"
                >
                  <AiFillInstagram className="text-2xl" />
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </>
  );
}

export default Navbar;
