import React from "react";
import { NoImageAddedIllustration } from '../../static/staticURLS'
function NoImageAdded() {
  return (
    <>
      <div
        className="hero min-h-screen"
        style={{ backgroundImage: `url(${NoImageAddedIllustration})`, objectFit: 'cover' }}
      >
        <div className="hero-overlay bg-opacity-60"></div>
        <div className="hero-content text-center text-neutral-content">
          <div className="max-w-md">
            <h1 className="mb-5 text-4xl font-bold">No Image Added Yet!</h1>
          </div>
        </div>
      </div>
    </>
  );
}

export default NoImageAdded;
