import React from "react";

// Import Icons
import { AiFillFacebook, AiFillInstagram } from "react-icons/ai";
import { BsYoutube } from "react-icons/bs";
import { Link } from "react-router-dom";
import { darkWebLogo, webLogo } from "../../../static/staticURLS";

function Footer() {
  var date = new Date();
  let year = date.getFullYear();
  return (
    <>
      <footer className="footer footer-center pb-5 bg-black text-primary-content">
  <div>
  <img src={darkWebLogo} alt='Still Minute Photography' className="darkFooterLogo" />
    <p>Copyright  {year} - All right reserved</p>
  </div> 
  <div>
  <div className="grid grid-flow-col gap-4">
            <ul className="home-about-social-links">
                <li className="social-icons">
                    <a
                    href="https://www.youtube.com/channel/UCZISLslvzpPI9ebD7lMWNEQ?view_as=subscriber"
                    target="_blank"
                    rel="noreferrer"
                    className="icon-colour  home-social-icons"
                    >
                    <BsYoutube className="text-2xl" />
                    </a>
                </li>
                <li className="social-icons">
                    <a
                    href="https://www.facebook.com/sminutep"
                    target="_blank"
                    rel="noreferrer"
                    className="icon-colour  home-social-icons"
                    >
                    <AiFillFacebook className="text-2xl" />
                    </a>
                </li>
                <li className="social-icons">
                    <a
                    href="https://www.instagram.com/stillminute_/"
                    target="_blank"
                    rel="noreferrer"
                    className="icon-colour  home-social-icons"
                    >
                    <AiFillInstagram className="text-2xl" />
                    </a>
                </li>
                </ul>
            </div>
  </div>
</footer>
    </>
  );
}

export default Footer;
          
