import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useLocation } from "react-router-dom";
// Static Files Import
import {
  Product_Cover,
  Films_Cover,
  FineArt_Cover,
  Food_Cover,
  Interiors_Cover,
  Events_Cover,
  Wedding_Cover
} from "../../static/staticURLS";

function ProductsFilmsInteriorsEventFoodFineArtHeader() {
  const [pageHeaderBackground, setPageHeaderBackground] = useState([]);
  const [pageHeaderText, setPageHeaderText] = useState([]);
  let location = useLocation();

  // Set the view of the page header based on url path
  useEffect(() => {
    // Product Background Image Banner
    if (location.pathname === "/Product") {
      setPageHeaderBackground(Product_Cover);
      setPageHeaderText(
        "Your brand identity is our utmost priority. We believe in weaving an effective concept into our photography so that your product stands out in the most creative way. A concept, your product, our creativity and your enormous win."
      );
    } else if (location.pathname === "/Films") {
      setPageHeaderBackground(Films_Cover);
      setPageHeaderText(
        "Welcome to the realm of capturing 24 photos per second. Films design and define the passage of time. It is the immersive journey through colours, people, atmosphere, music and stories. Our experience ranges from documentaries, short films, music videos, brand videos to trailers for events. We are here to tell your stories. Lights, Still Minute...and Action!"
      );
    } else if (location.pathname === "/Interiors") {
      setPageHeaderBackground(Interiors_Cover);
      setPageHeaderText(
        "The space we live in is considered a reflection of an individuals personality. This generates a story with elements of shape, texture, material and light, carefully chosen and controlled by the designer. We transcribe the designers vision to images that create immersive experiences of the space and its story."
      );
    } else if (location.pathname === "/Events") {
      setPageHeaderBackground(Events_Cover);
      setPageHeaderText(
        "Events are a footprint of our time and the culture we propagate. Each event shows the rich culture of our origins. We at Still Minute, make sure your talent is never unaccounted for. What are even events if they werent captured? Music, dance, ethnic and curated events, we keep shooting as you keep evolving."
      );
    } else if (location.pathname === "/Food") {
      setPageHeaderBackground(Food_Cover);
      setPageHeaderText(
        "We believe that food is first consumed by the eyes. Chefs thought behind every dish into the image, coupled with thorough research on its ingredients, origins and careful selections of props with utmost authenticity is our forte. Hence the love for food photography. Each gourmet now has a story to tell."
      );
    } else if (location.pathname === "/FineArt") {
      setPageHeaderBackground(FineArt_Cover);
      setPageHeaderText(
        "Abstract, colour, space, philosophy, time, shape, thought, illusions, dreams, paradox, surreal, unreal and real. Welcome to the voices of Akash and Siddharth and their perception of the world."
      );
    }
    else if(location.pathname === "/Weddings"){
      setPageHeaderBackground(Wedding_Cover);
      setPageHeaderText(
        "The happiness of two families uniting, grandparents surprising everyone with their hidden talent and funny dance moves, kids turning the place into their playground, the dazzling decorations, endless ceremonies, an exhilarating pool of colours and flowers, the tears of joy and sadness as you step into a new life. The day goes by and you wish for it to never end. But we make sure, that you relive this day, for generations to come. Let us capture your dream."
      );
    }
    
    else {
      setPageHeaderBackground(Product_Cover);
      setPageHeaderText(
        "Your brand identity is our utmost priority. We believe in weaving an effective concept into our photography so that your product stands out in the most creative way. A concept, your product, our creativity and your enormous win."
      );
    }
  }, [location]);
  let title = location.pathname.slice(1);
  
  if(title === 'FineArt'){
    title = 'FINE ART'
  }

  return (
    <>
      <div
        className="hero min-h-screen header_bg"
        style={{ backgroundImage: `url(${pageHeaderBackground})` }}
      >
        <div className="hero-overlay bg-black bg-opacity-50 header_bg_opacity"></div>
        <div className="hero-content text-center text-neutral-content headerText">
          <div className="mt-0 lg:mt-96">
            <h1 className="mb-12 lg:text-8xl md: text-5xl sm: text-5xl font-primary leading-loose">{title.toUpperCase()}</h1>
            <h3 className="mb-5 lg:text-xl md: text-md sm: text-sm">{pageHeaderText}</h3>
          </div>
        </div>
      </div>
    </>
  );
}

export default ProductsFilmsInteriorsEventFoodFineArtHeader;
