// Import dependencies
import React, { useState, useEffect, useCallback } from "react";
import { useLocation } from "react-router-dom";
import Carousel, { Modal, ModalGateway } from "react-images";
import PhotographyGallery from "./PhotographyGallery";
import axios from "axios";
import NoImageAdded from "./NoImageAdded";
import { AWS_Django_API_URL } from "../../static/staticURLS";

function ProductFoodFineArtLowerSection() {
  let location = useLocation();
  let pathName = location?.pathname?.slice(1);
  const [images, setImages] = useState([]);
  const [error, setError] = useState(false);
  const [loading, setLoading] = useState(false);

  // Dynamically fetch the Photos links based on pathname from BE & store it in photos variable
  let fetchImagesBasedURL = async () => {
    let fetchedImages;
    if (pathName.includes("Events")) {
      let category = pathName.slice(
        pathName.lastIndexOf("/") + 1,
        pathName.length
      );
      fetchedImages = await axios(`/api/Events/${category}`, { crossdomain: true });
    } else if (pathName.includes("Interiors")) {
      let category = pathName.slice(
        pathName.lastIndexOf("/") + 1,
        pathName.length
      );
      fetchedImages = await axios.get(`/api/Interiors/${category}`, { crossdomain: true });
    } else if (pathName.includes("Weddings")) {
      let category = pathName.slice(
        pathName.lastIndexOf("/") + 1,
        pathName.length
      );
      fetchedImages = await axios.get(`/api/Weddings/${category}`, { crossdomain: true });
    } else {
      fetchedImages = await axios.get(`/api/${pathName}/`, { crossdomain: true });
    }
    if (fetchedImages?.status === 200 ) {
      setImages(fetchedImages?.data);
    } else {
      // Error while data fetching
      setImages([]);
      setError(true);
      setLoading(false);
    }
  };

  useEffect(() => {
    setLoading(true);
    fetchImagesBasedURL();
    setLoading(false);
  }, [location, loading]);

  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const openLightbox = useCallback((event, index) => {
    setCurrentImage(event);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };
  return (
    <div className="container mx-auto">
      {loading ? (
        <div class="spinner"></div>
      ) : (
        (images?.length < 1 || !images)?
        <>
        <NoImageAdded />
        </>:
        <>
          <PhotographyGallery data={images} openLightbox={openLightbox} />
          <ModalGateway>
            {viewerIsOpen ? (
              <Modal onClose={closeLightbox}>
                <Carousel
                  components={{ isFullscreen: true }}
                  currentIndex={currentImage}
                  views={images?.map((x) => ({
                    ...x,
                    source: x?.Image
                    // srcset: x?.src,
                    // caption: x.title
                  }))}
                />
              </Modal>
            ) : null}
          </ModalGateway>
        </>
      )}
    </div>
  );
}

export default ProductFoodFineArtLowerSection;
