import React from "react";
import { ContactPage, ContactPageAddress } from "../../static/staticURLS";
import { AiOutlineWhatsApp } from "react-icons/ai";
import emailjs from "@emailjs/browser";
import { useRef } from "react";
import { toast } from "react-toastify";

function Contact() {
  const form = useRef();
  const sendEmail = (e) => {
    e.preventDefault();
    emailjs
      .sendForm(
        process.env.REACT_APP_EMAILJS_SERVICE_ID,
        process.env.REACT_APP_EMAILJS_TEMPLATE_ID,
        form.current,
        process.env.REACT_APP_EMAILJS_PUBLIC_ID
      )
      .then(
        () => {
          toast.success("Message successfully sent!");
        },
        () => {
          toast.error("Failed to send the message, please try again");
        }
      );
  };
  return (
    <>
      <section className="relative block bg-gray-0 text-black">
        {/* <div className="container mx-auto px-4 lg:pt-24">
          <div className="flex flex-wrap text-center justify-center">
            <div className="w-full lg:w-6/12 px-4">
              <h2 className=" lg:text-4xl md: text-3xl sm: text-2xl pt-12 font-semibold text-black">
                Your brand has our undivided attention!
              </h2>
            </div>
          </div>
          <div className="flex flex-wrap mt-12 justify-center">
            <div className="w-full lg:w-3/12 px-4 text-center">
              <div className="text-black p-3 w-12 h-12 shadow-lg rounded-full bg-white inline-flex items-center justify-center">
                <i className="fas fa-medal text-xl"></i>
              </div>
              <h6 className="text-xl mt-5 font-semibold text-black">
                Excelent Services
              </h6>
              <p className="mt-2 mb-4 text-gray-500">
                Some quick example text to build on the card title and make up
                the bulk of the card's content.
              </p>
            </div>
            <div className="w-full lg:w-3/12 px-4 text-center">
              <div className="text-black p-3 w-12 h-12 shadow-lg rounded-full bg-white inline-flex items-center justify-center">
                <i className="fas fa-poll text-xl"></i>
              </div>
              <h5 className="text-xl mt-5 font-semibold text-black">
                Grow your market
              </h5>
              <p className="mt-2 mb-4 text-gray-500">
                Some quick example text to build on the card title and make up
                the bulk of the card's content.
              </p>
            </div>
            <div className="w-full lg:w-3/12 px-4 text-center">
              <div className="text-black p-3 w-12 h-12 shadow-lg rounded-full bg-white inline-flex items-center justify-center">
                <i className="fas fa-lightbulb text-xl"></i>
              </div>
              <h5 className="text-xl mt-5 font-semibold text-black">
                Launch time
              </h5>
              <p className="mt-2 mb-4 text-gray-500">
                Some quick example text to build on the card title and make up
                the bulk of the card's content.
              </p>
            </div>
          </div>
        </div> */}
        <div className="container mx-auto">
          <div className="hero min-h-screen/2 shadow-lg rounded-lg bg-gray-00">
            <form ref={form} onSubmit={sendEmail}>
              <div className="hero-content flex-col lg:flex-row-reverse mb-12 mt-12">
                <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-gray-300">
                  <div className="flex-auto p-5 lg:p-10">
                    <h4 className="text-2xl font-semibold text-900">
                      Want to connect?
                    </h4>
                    <p className="leading-relaxed mt-1 mb-4 text-gray-600">
                      Please fill the form and we'll get back to you ASAP.
                    </p>
                    <div className="relative w-full mb-3 mt-8">
                      <label
                        className="block uppercase text-black text-xs font-bold mb-2"
                        htmlFor="full-name"
                      >
                        Full Name
                      </label>
                      <input
                        type="text"
                        name="name"
                        required
                        className="border-0 px-3 py-3 placeholder-gray-400 text-black bg-white rounded text-sm shadow focus:outline-none focus:ring w-full"
                        placeholder="Full Name"
                        style={{ transition: "all .15s ease" }}
                      />
                    </div>

                    <div className="relative w-full mb-3">
                      <label
                        className="block uppercase text-black text-xs font-bold mb-2"
                        htmlFor="email"
                      >
                        Email
                      </label>
                      <input
                        type="email"
                        className="border-0 px-3 py-3 placeholder-gray-400 text-black bg-white rounded text-sm shadow focus:outline-none focus:ring w-full"
                        placeholder="Email"
                        name="email"
                        required
                        style={{ transition: "all .15s ease" }}
                      />
                    </div>

                    <div className="relative w-full mb-3">
                      <label
                        className="block uppercase text-black text-xs font-bold mb-2"
                        htmlFor="message"
                      >
                        Message
                      </label>
                      <textarea
                        rows="4"
                        cols="80"
                        name="message"
                        required
                        className="border-0 px-3 py-3 placeholder-gray-400 text-black bg-white rounded text-sm shadow focus:outline-none focus:ring w-full"
                        placeholder="Type a message..."
                      />
                    </div>
                    <div className="text-center mt-6">
                      {/* <button
                        className="bg-gray-900 text-white active:bg-gray-700 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1"
                        type="button"
                        style={{ transition: "all .15s ease" }}
                      >
                        Send Message
                      </button> */}
                      <input
                        type="submit"
                        className="bg-gray-900 text-white active:bg-gray-700 text-sm font-bold uppercase px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 submitBtn"
                        value="Send Message"
                      />
                    </div>
                  </div>
                </div>
                <div className="text-black pr-32 lg: -mt-44">
                  <h1 className="lg:text-3xl md: text-xl sm: text-xl pb-3">
                    ADDRESS
                  </h1>
                  <div class="horizontal-line"></div>
                  <h3 className="mt-2 font-secondary text-base xl:text-xl pb-8">
                    {ContactPageAddress}
                  </h3>
                  <h1 className="lg:text-3xl md: text-xl sm: text-xl pb-3">
                    CONTACT
                  </h1>
                  <div class="horizontal-line"></div>
                  {ContactPage?.map((contacts) => {
                    return (
                      <div className="flex inline-block mt-2 text-black">
                        <a
                          className="flex items-center lg:text-xl md: text-sm sm: text-sm"
                          href={contacts?.PhNumberLink}
                          target="_blank"
                        >
                          <AiOutlineWhatsApp className="text-xl" />
                          <h3 className="lg:text-xl md: text-sm sm: text-sm pl-3">
                            {contacts?.Name}: {contacts?.PhNumber}
                          </h3>
                        </a>
                      </div>
                    );
                  })}
                </div>
              </div>
            </form>
          </div>
        </div>
      </section>
    </>
  );
}

export default Contact;
