import React from 'react'

function PhotographyGallery(props) {
  return (
    <div className="gallery mt-12">
        {props?.data?.map((pic, index) => (
          <>
              <div
                className="pics"
                key={pic?.id}
                onClick={()=>props?.openLightbox(index)}
              >
                <img
                  src={pic?.Image}
                  alt={pic?.PictureName}
                  className="galleryImage"
                />
              </div>
            </>
          ))}
      </div>
  )
}

export default PhotographyGallery