import React from "react";
import { HomePage } from "../../static/staticURLS";
import Clientele from "../common/Clientele";
import HomeTopScreen from "../HomeTopScreen";
function Home() {
  let homePage = HomePage;
  return (
    <>
    {/* Home screen Top Component */}
    <HomeTopScreen homePage={homePage} />

    {/* Clientele */}
    <Clientele />
    </>
  );
}

export default Home;
