import React from "react";
import ProductsFilmsInteriorsEventFoodFineArtHeader from "../common/ProductsFilmsInteriorsEventFoodFineArtHeader";
import { Fade } from "react-awesome-reveal";
import { useState } from "react";
import { useEffect } from "react";
import { useLocation } from "react-router-dom";
import axios from "axios";
import { AWS_Django_API_URL } from "../../static/staticURLS";

function Films() {
  const [youtubeLinks, setYoutubeLinks] = useState([]);
  const [error, setError] = useState([]);
  const [loading, setLoading] = useState(false);
  const location = useLocation();
  let pathName = location?.pathname?.slice(1);

  // Dynamically fetch the Photos links based on pathname from BE & store it in photos variable
  let fetchImagesBasedURL = async () => {
    setLoading(true);
    let fetchedImages = await axios.get(
      `/api/${pathName}/`
      , { crossdomain: true });
    if (fetchedImages?.status === 200) {
      setYoutubeLinks(fetchedImages?.data);
      setLoading(false);
    } else {
      // Error while data fetching
      setYoutubeLinks([]);
      setError(true);
      setLoading(false);
    }
  };

  useEffect(() => {
    if (pathName === "Films") {
      fetchImagesBasedURL();
      setLoading(false);
    }
  }, [location]);

  return (
    <>
      {/* ==== Top Head section starts here === */}
      <ProductsFilmsInteriorsEventFoodFineArtHeader />
      {/* ==== Top Head section ends here === */}

      {/* ==== Lower section ends here === */}
      <div className="grid grid-cols-1 bg-unsplash-[3scV_o_GRN0]">
        <div className="container mx-auto">
          {loading ? (
            <div class="spinner"></div>
          ) : (
            <>
              <div className="grid sm:grid-cols-1 md:grid-cols-2 lg:grid-cols-2 mt-12 gap-8 pb-12">
                {youtubeLinks?.map((youtubeLink) => (
                  <>
                    <Fade direction="up" key={youtubeLink?.id}>
                      <div>
                        <iframe
                          id="ytplayer"
                          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture full"
                          type="text/html"
                          className="YoutubeVideoGrid animate__animated animate__slideInUp"
                          src={youtubeLink?.YoutubeEmbeddLink}
                          frameborder="0"
                        ></iframe>
                      </div>
                    </Fade>
                  </>
                ))}
              </div>
            </>
          )}
        </div>
      </div>
    </>
  );
}

export default Films;
