import React from "react";
import { Link } from "react-router-dom";

function HomeTopScreen({ homePage }) {
  console.log(homePage, "homePage");
  return (
    <div className="p-3">
      <div className="grid max-w-screen gap-2 row-gap-2 mb-2 sm:grid-cols-2 lg:grid-cols-3 sm:mx-auto">
        {homePage?.map((categories, index) => {
          return (
            <>
              <Link to={categories?.navLink}>
                <div className="hoverHomeImage relative group">
                  <img
                    className="object-cover w-full h-96 rounded shadow-lg"
                    src={categories?.coverImage}
                    alt=""
                  />
                  <div class="opacity-0 group-hover:opacity-80 duration-300 absolute left-0 bottom-0 h-96 w-full right-0 z-10 flex justify-center items-center text-5xl bg-gray-900 text-white font-bold">
                    {categories?.category}
                  </div>
                </div>
              </Link>
            </>
          );
        })}
      </div>
    </div>
  );
}

export default HomeTopScreen;
